import "./Section.css";


function Titel(prop){
    return(
        <div className="titel-container" id={prop.id}>
            <video src='/videos/Atano2.mp4' className="video" autoPlay loop muted />
            <div className="atano-name-container">
                <img src="/images/AtanoSituatie01.png" alt= "Atano Name" className="atano-name"/>
            </div>
            
        </div>
            
    )
}

export default Titel;