import "./Section.css";
import { Parallax } from 'react-parallax'

function Tactiek(prop){


    function appear(x,start,end,piek){
        var a =-Math.abs((x-0.5*(end-start)-start)-(x-0.5*(end-start)-start)%((end-start)/2))*100;
        var b = Math.sin((x-0.25*(1-start/end)*end-start)*6.28/(end-start));
        var c = (b+1)/piek;

        return (Math.abs(a+c)+(a+c))/2;
    }
    return(
        <div id={prop.id}>
            
            <Parallax
                className="tactiek-container"
                renderLayer={percentage =>(
                    <div>
                        <img
                            src="images/AtanoTactiek02.png"
                            className="tactiek-logo"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,0.8,1.1,1.4)}`
                                }}
                        />
                        <img
                            src="images/AtanoTactiek05.png"
                            className="tactiek5"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,0.8,1.3,2)}`,
                                transform: `translate(-85%,${percentage*180}%)`, 
                                }}
                        />
                        <img
                            src="images/AtanoTactiek06.png"
                            className="tactiek6"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,0.8,1.3,1.4)}`,
                                transform: `translate(-15%,${percentage*180}%)`, 
                                }}
                        />
                        <img
                            src="images/AtanoTactiek01.png"
                            className="tactiek-logo"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,1.0,1.3,1.4)}`
                                }}
                        />
                        {/* <img
                            src="images/AtanoTactiek07.png"
                            className="tactiek7"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,1.0,1.3,1.4)}`,
                                transform: `translate(-90%,${percentage*80}%)`, 
                                }}
                        /> */}
                        <img
                            src="images/AtanoTactiek09.png"
                            className="tactiek9"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,1.0,1.6,2)}`,
                                transform: `translate(${+20-percentage*20}%,${percentage*150}%)`, 
                                }}
                        />
                        <img
                            src="images/AtanoTactiek03.png"
                            className="tactiek-logo"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,1.2,1.5,1.4)}`
                                }}
                        />
                          <img
                            src="images/AtanoTactiek10.png"
                            className="tactiek10"
                            alt="Atano Logo"
                            style = {{  
                                opacity:`${appear(percentage,1.25,1.75,1.4)}`,
                                transform: `translate(-50%,${percentage*100}%)`, 
                                }}
                        />
                    </div>
                )}
            />
        </div>
    )
}

export default Tactiek