import "./Section.css"
import {Parallax} from "react-parallax"

function Voorbereiding(prop){
    function appear(x,start,end){
        return Math.sin((x-start)*3.5/(end-start))*1.25;
    }
    return(
        <div id={prop.id}>
            <Parallax
                className="voorbereiding-container"
                renderLayer = {percentage =>(
                    <div>
                        <img
                            src="images/AtanoVoorbereiding.png"
                            className="voorbereiding-controller"
                            alt="Atano controller"
                            style = {{ 
                                transform: `translate(${percentage*350-475}%,0) `, 
                                }}
                        />
                        <h1>
                            <div
                                className="main-text"
                                style = {{ 
                                    transform: `translate(-50%,0) scale(${0.9+0.1*appear(percentage,0.5,1.3)})`, 
                                    opacity:`${appear(percentage,0.5,1.3)}` 
                                    }}
                            >
                                <h2>De ochtend</h2>
                               In de ochtend bepalen we onze tatiek voor die dag
                            </div>
                            <div
                                className="voorbereiding-tijd"
                                style = {{ 
                                    transform: `scale(${0.9+0.1*appear(percentage,0.9,1.5)})`, 
                                    opacity:`${appear(percentage,0.9,1.5)}`
                                    }}
                            >
                                tijd<br/>
                                Meer tijd in je werkplaats<br/>
                                of<br/>
                                Eerder op je toren zijn om een draak aan te vangen.<br/>
                            </div>
                            <div
                                className="voorbereiding-taktiek"
                                style = {{ 
                                    transform: `scale(${0.9+0.1*appear(percentage,1.0,1.7)})`, 
                                    opacity:`${appear(percentage,1.15,1.7)}`
                                    }}
                            >
                                Taktiek<br/>
                                Ieder dag kies je weer voor een geschikte  taktiek.
                            </div>
                        </h1>
                        
                    </div>
                )}
            />
        </div>
    )
}

export default Voorbereiding;